.false-idol-logo {
	max-width: 280px;
	@media (min-width: 1024px) {
		float: left;
		max-width: 350px;
		margin-top: -183px;
	}
}


.footer-bottom {
	padding-bottom: 20px;
}

.footer-menu ul li {
	padding: 2px 10px;
}

.footer-social {
	margin-right: 20%;
	img {
		max-width: 30px;
		margin-top: 20px;
		margin-bottom: 5px;
		padding: 0 1%;
	}
}


